import React from 'react';
import  '../css/List.css'
import image from '../img/Vector.svg'


const List = props => {
  return (
    <ul className="Lists">
      {props.todos.map((todo,i) => ( //受け取ったtodosをmapメソッドにて展開
        <li key={i}>
          <p className="todo_item">{todo}</p>
          <button className="delete" onClick={() => props.handleRemove(i)}>
            <img src={image}/>
          </button>
        </li>
      ))}
    </ul>
  )
}

export default List;