import React from 'react';
import  '../css/Form.css'
import addImage from '../img/add.svg'



const Form = props => {   //引数でAppコンポーネントからpropsを受け取る。
  return (
    <form onSubmit={props.handleSubmit} className="form">
      <input
        type="text"
        onChange={props.handleChange}
        value={props.value}
      />
      <button type="submit">
        <img src={addImage}/>
      </button>
    </form>
  )
}

export default Form;